const errorMsg = {
  400: '(400)',
  401: '(401)',
  403: '(403)',
  404: '(404)',
  408: '(408)',
  500: '(500)',
  501: '(501)',
  502: '(502)',
  503: '(503)',
  504: '(504)',
  505: 'HTTP(505)'
}

export default function (httpCode) {
  if (Object.keys(errorMsg).includes(httpCode.toString())) {
    return errorMsg[httpCode]
  }
  return `(${httpCode})`
}
